import { InnerBlocks, useBlockProps } from "@wordpress/block-editor";
import { prefix } from '../../../constants';
import classNames from 'classnames';

const Save = (props) => {
    const {
        attributes
    } = props;

    const {
        boxShadow,
        activeLayout,
        block_id,
        border
    } = attributes;

    const wrapperClassName = classNames(
        'wp-block-rise-blocks-profile-cards',
        `${prefix}-save`,
        `${prefix}-${activeLayout}-layout`,
        { [`${prefix}-shadow-enable`]: boxShadow },
        { [`${prefix}-border-enable`]: border }
    );

    const blockProps = useBlockProps.save({
        'id': block_id
    });

    return (
        <div {...blockProps}>
            <div className={wrapperClassName}>
                <div className={`${prefix}-block-lists`}>
                    <InnerBlocks.Content />
                </div>
            </div>
        </div>
    );
}

export default Save;