import "../styles/editor.scss";

import { __ } from "@wordpress/i18n";
import { withInstanceId } from "@wordpress/compose";
import { useBlockProps, InnerBlocks } from "@wordpress/block-editor";
import { useEffect } from "@wordpress/element";

import { prefix } from "../../../constants";
import memoize from "memize";
import times from "lodash/times";
import classNames from "classnames";

import Inspector from "./inspector";
import {
	shouldShowAddButton,
	setInitialAttrs,
} from "../../../helpers";
import { editorNSaveAttrs } from "../constants";
import ParentBlockSelector from "../../../components/parent-block-selector";

const ALLOWED_BLOCKS = ["rise-blocks/profile-card"];

const getCount = memoize((count) => {
	return times(count, () => ["rise-blocks/profile-card"]);
});

const Edit = (props) => {
	const { attributes, isSelected, clientId, setAttributes, instanceId } = props;
	const { activeLayout, count, boxShadow, border, isInit, itemsPerRow } =
		attributes;

	/*--------------------------------------------------------*
	 * Set Initial Attributes
	 * Set the default value of attributes once. it's helps
	 * to set default value on server side too.
	 *---------------------------------------------------------*/
	if (!isInit) {
		setInitialAttrs(props, editorNSaveAttrs);
		return null;
	}

	useEffect(() => {
		/*-------------------------------------------------------*
		 * Block_id must be  update on componentDidmount
		 * otherwise we will get same block_id while duplicating
		 * Blocks.( Parent BLocks)
		 *-------------------------------------------------------*/
		setAttributes({
			block_id: `${prefix}-${instanceId}-${clientId}`,
		});
	}, []);

	const wrapperClassName = classNames(
		"wp-block-rise-blocks-profile-cards",
		`${prefix}-${activeLayout}-layout`,
		`${prefix}-items-per-row-${itemsPerRow}`,
		{ [`${prefix}-border-enable`]: border },
		{ [`${prefix}-shadow-enable`]: boxShadow },
	);
    
	const blockProps = useBlockProps();

	return (
		<>
			{isSelected && <Inspector {...props} />}

			<div {...blockProps}>
				<div className={wrapperClassName}>
					<ParentBlockSelector icon="profileCard" label="Profile Cards" />
					<InnerBlocks
						template={getCount(count)}
						allowedBlocks={ALLOWED_BLOCKS}
					/>
				</div>
			</div>
    </>
  );
};

export default withInstanceId(Edit);
