import { __ } from "@wordpress/i18n";
import { prefix } from "../../../constants";
import {
	shouldShowAddButton,
} from "../../../helpers";
import { Button } from "@wordpress/components";
import { createBlock } from "@wordpress/blocks";

import {
	InspectorControls,
	InspectorAdvancedControls,
} from "@wordpress/block-editor";
import { select, dispatch } from "@wordpress/data";
import { PanelBody, ToggleControl, RangeControl } from "@wordpress/components";

import LayoutsIcons from "../../../components/layouts-icon";
import { profileCardLayouts } from "../constants";
import Exporter from "../../../components/exporter";

const InspectorPanel = ({ clientId, setAttributes, attributes }) => {
	const { gutter, itemsPerRow, activeLayout, boxShadow, border, count } = attributes;

	const updateInnerBlocksAttributes = (clientId, newActiveLayout) => {
		const innerBlocks =
			select("core/block-editor").getBlock(clientId).innerBlocks;

		innerBlocks.forEach((block) => {
			const { clientId: blockClientId } = block;
			const updatedAttributes = {
				...block.attributes,
				activeLayout: newActiveLayout,
			};
			dispatch("core/block-editor").updateBlockAttributes(
				blockClientId,
				updatedAttributes,
			);
		});
	};

	return (
		<>
			<InspectorAdvancedControls>
				<Exporter clientId={clientId} />
			</InspectorAdvancedControls>
			<InspectorControls>
			<PanelBody title={__("General", "rise-blocks")} initialOpen={true}>
				{shouldShowAddButton(clientId) && (
					<Button
					className={`${prefix}-add-new-item`}
					isLarge
					label={__("Add Profile Card", "rise-blocks")}
					icon="insert"
					onClick={() => {
						const created = createBlock("rise-blocks/profile-card");
						dispatch("core/block-editor").insertBlock(
							created,
							undefined,
							clientId,
							);
						setAttributes({ count: count + 1 });
					}}
					>
					{__("Add New Profile Cards", "rise-blocks")}
					</Button>
					)}
					<LayoutsIcons
						options={profileCardLayouts.layouts}
						activeLayout={activeLayout}
						label={__("Layouts", "rise-blocks")}
						onChange={(newActiveLayout) => {
							updateInnerBlocksAttributes(clientId, newActiveLayout);
							setAttributes({ activeLayout: newActiveLayout });
						}}
					/>

					<RangeControl
						label={__("Number of Items per Row", "rise-blocks")}
						value={itemsPerRow}
						onChange={(itemsPerRow) => {
							setAttributes({ itemsPerRow });
						}}
						min={1}
						max={4}
					/>

					<ToggleControl
						label={__("Enable/Disable Shadow")}
						checked={boxShadow}
						onChange={(boxShadow) => setAttributes({ boxShadow })}
					/>

					<ToggleControl
						label={__("Enable/Disable border")}
						checked={border}
						onChange={(border) => setAttributes({ border })}
					/>

					<RangeControl
						label={__("Gutter", "rise-blocks")}
						help={__("Gap between icon boxes ( Frontend )", "rise-blocks")}
						value={gutter}
						onChange={(gutter) => {
							if (gutter === 0) setAttributes({ gutter, boxShadow: false });
							else setAttributes({ gutter, boxShadow: true });
						}}
						min={0}
						max={40}
					/>
				</PanelBody>
			</InspectorControls>
		</>
	);
};

export default InspectorPanel;
